nav {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: row wrap;
	    flex-flow: row wrap;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: center;
	    align-items: center;
	margin-left: 3%;
	margin-right: 3%;
	padding-top: 10px;
	padding-bottom: 10px;
}
@media (min-width: 800px) and (max-width: 1500px) {
	nav {
		-ms-flex-pack: center;
		    justify-content: center;
	}
}
@media (max-width: 1500px) {
	nav {
		margin-left: 0;
		margin-right: 0;
	}
}


