div.map-side {
    width: 100%;
    display: -ms-flexbox;
    display: flex; 
    -ms-flex-flow: column nowrap; 
        flex-flow: column nowrap;
    -ms-flex-align: center;
        align-items: center;
    padding: 5%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
div.map-side h3 {
    text-transform: uppercase;
    color: #404040;
    margin: 0;
    font-family: 'Teko';
    font-size: 2em;
    font-weight: normal;
}
div.map-side hr {
    width: 100%;
    height: 2px;
    background-color: rgb(235, 239, 227);
}
div.map-side hr + div {
    padding-top: 5%;
}


