div.inner-grid {
    display: grid;
    grid-template-columns: 32% 32% 32%;
    grid-template-rows: auto auto;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    grid-gap: 3%;
    margin-bottom: 3.5%;
    grid-auto-flow: column;
    -ms-flex-align: stretch;
        align-items: stretch;
}
img.news-item-img {
	width: 100%;
	height: auto;
	margin-bottom: 5px;
    cursor: pointer;
}
div.news-item-desc {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: row nowrap;
	    flex-flow: row nowrap;
	-ms-flex-align: center;
	    align-items: center;
	border: 2px solid #404040;
}
div.news-item-desc a {
	color: #404040;
	text-decoration: none;
	-webkit-transition: all 0.2s ease-in-out;
	-o-transition: all 0.2s ease-in-out;
	transition: all 0.2s ease-in-out;
}
div.news-item-desc a:hover {
	color: #3A8013;
	text-decoration: underline;
	-webkit-transition: all 0.2s ease-in-out;
	-o-transition: all 0.2s ease-in-out;
	transition: all 0.2s ease-in-out;
}
div.desc-col {
	margin-left: 5%;
	margin-right: 5%;
	color: #404040;
}
div.news-item-desc div.desc-col:nth-of-type(1) {
	font-family: 'Teko', Arial, sans-serif;
	font-size: 1.75em;
	font-weight: normal;
}
div.news-item-desc div.desc-col:nth-of-type(2) {
	border-right: 1px solid #404040;
	height: 50px;
}
div.news-item-desc div.desc-col:nth-of-type(3) {
	font-size: 1.2em;
    font-weight: bold;
}
@media (max-width: 900px) {
    div.inner-grid {
        grid-template-columns: auto;
        grid-auto-flow: row;
    }
    div.news-item-desc {
        margin-bottom: 10%;
    }
}


