div#btn-and-icons {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: row nowrap;
	    flex-flow: row nowrap;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: center;
	    align-items: center;
}
div#btn-and-icons button {
	margin-right: 100px;
	background-color: white;
	color: #3A8013;
	text-transform: uppercase;
	border: 2px solid #3A8013;
	padding: 15px;
	font-weight: bold;
	cursor: pointer;
	-webkit-transition: border-color 0.2s ease-in-out;
	-o-transition: border-color 0.2s ease-in-out;
	transition: border-color 0.2s ease-in-out;
}
div#btn-and-icons button:hover {
	border-color: #404040;
}
@media (min-width: 799px) and (max-width: 1000px) {
	div#btn-and-icons {
		-ms-flex-pack: center;
		    justify-content: center;
	}
	div#btn-and-icons button {
		padding: 10px;
		margin-right: 25px;
	}
}
@media (max-width: 799px) {
	div#btn-and-icons button {
		display: none;
	}
	div#btn-and-icons div.icon-group span:not(:last-of-type) {
		display: none;
	}
}


