div#toggle-mobile-menu > div.icon-group {
	display: none;
}
@media (max-width: 799px) {
	div#toggle-mobile-menu > div.icon-group {
		display: -ms-flexbox;
		display: flex;
		-ms-flex-flow: row nowrap;
		    flex-flow: row nowrap;
		-ms-flex-pack: center;
		    justify-content: center;
		margin-top: 40px;
		padding-top: 10px;
		padding-bottom: 10px;
		background-color: rgb(249, 252, 243);
	}
}


