div.nav-left {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: row nowrap;
	    flex-flow: row nowrap;
	-ms-flex-pack: start;
	    justify-content: flex-start;
	-ms-flex-align: center;
	    align-items: center;
}
div.nav-left p, div.nav-left span {
	font-weight: bold;
}
div.nav-left span {
	color: #3A8013;
}
div#logo {
	background: url('../../img/liber-logo.png');
	width: 140px;
	height: 85px;
}
@media (max-width: 1500px) {
	div#logo {
		margin-left: 2.5%;
	}
}
@media (max-width: 799px) {
	div.nav-left span {
		display: none;
	}
}


